// card styles
.card {
  width: 330px;
  height: 254px;
  border-radius: 10px;
  background-color: white;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;

  img {
    margin: auto;
    display: block;
    height: 70px;
    width: 70px;
    border-radius: 50%;
  }

  p {
    line-height: 26px;
    text-align: center;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
}


@media screen and (min-width: 768px) {
  .card {
    width: 344px;
  }
}

@media screen and (min-width: 1024px) {
  .card {
    width: 282px;
  }
}

@media screen and (min-width: 1170px) {
  .card {
    width: 370px;
  }
}

